  ul{
    list-style: none;
    color: #5F7086;
  }
  .footer{
    padding: 100px 0;
    display: flex;
  }

  .footer-p{
    color: #5F7086;
    margin-top: 240px;
  }

  .footer-col1{
    font-size: 18px;
    width:430px;
    margin-left: auto;
    margin-right: auto;
  }

  .footer-col2{
    display: flex;
    margin-left:auto;
    margin-right: auto;
    width:320px;
  }

  .footer-col2-1{
    margin-left: 10%;
  }

  .footer-col2-2{
    margin-left: 30%;
  }

  .footer-col2-1 h4,.footer-col2-2 h4{
      font-size: 18px;
      text-transform: capitalize;
      margin-bottom: 35px;
      font-weight: 500;
      position: relative;
  }
  .footer-col2-1 h4::before,.footer-col2-2 h4::before{
      content: '';
      position: absolute;
      left:0;
      bottom: -10px;
      background-color: #4094EC;
      height: 2px;
      box-sizing: border-box;
      width: 50px;
  }
  .footer-col2-1 ul li:not(:last-child),.footer-col2-2 ul li:not(:last-child){
      margin-bottom: 10px;
  }
  .footer-col2-1 ul li a,.footer-col2-2 ul li a{
      font-size: 20px;
      text-transform: capitalize;
      text-decoration: none;
      font-weight: 300;
      color: #5F7086;
      display: block;
      transition: all 0.3s ease;
  }
  .footer-col2-1 ul li a:hover,.footer-col2-2 ul li a:hover{
      color: #000000;
      padding-left: 8px;
  }
  .footer-col2-1 .social-links a,.footer-col2-2 .social-links a{
      display: inline-block;
      height: 40px;
      width: 40px;
      background-color: rgba(255,255,255,0.2);
      margin:0 10px 10px 0;
      text-align: center;
      line-height: 40px;
      border-radius: 50%;
      transition: all 0.5s ease;
  }
  .footer-col2-1 .social-links a:hover,.footer-col2-2 .social-links a:hover{
      color: #24262b;
      background-color: #ffffff;
  }
  
